@import 'src/common/styles/scss/mixins';

.sliderContainer {
  @include flex(space-between, center);
  gap: 20px;
  @include fontSettings(16px, 600, 20px);

  .number {
    border: 1px solid #E8E8E8;
    width: 36px;
    height: 36px;
    background-color: white;
    @include flex(center, center);
  }
}